let radius = mobileAndTabletCheck() ? 3 : 7;

const geoMarkerStyle = new Style({
  image: new CircleStyle({
    radius: radius,
    fill: new Fill({color: 'black'}),
    stroke: new Stroke({
      color: 'white',
      width: 2,
    }),
  })
});


var drawCircleInMeter = (center, radius) => {
  var edgeCoordinate = [center[0] + radius, center[1]];
  var groundRadius = getDistance(
      transform(center, 'EPSG:3857', 'EPSG:4326'),
      transform(edgeCoordinate, 'EPSG:3857', 'EPSG:4326'),
      6378137 // earth radius
  );

  var circleFeature;
  if (center) {
    var circle = new Circle(center, groundRadius);
    circleFeature = new Feature(circle);
  } else {
    circleFeature = new Feature();
  }

  var vectorSource = new VectorSource({
    features: [circleFeature],
    projection: 'EPSG:3857',
  });

  return new VectorLayer({
    source: vectorSource,
    style: [
    new Style({
        stroke: new Stroke({
            color: 'blue',
            width: 3
        }),
        fill: new Fill({
            color: 'rgba(0, 0, 255, 0.1)'
        })
    })]
  });
}


function drawPosition(event, geolocation) {
  let position = geolocation.getPosition();
  if (!position)
    return;
  var vectorContext = getVectorContext(event);
  var currentPoint = new Point(position);
  var feature = new Feature(currentPoint);
  vectorContext.drawFeature(feature, geoMarkerStyle);
};

var changeCirclePosition = function(center, radius, circleLayer) {
  var circle = new Circle(center, radius);
  var circleFeature = new Feature(circle);
  circleLayer.getSource().clear();
  circleLayer.getSource().addFeatures([circleFeature]);
};


